<template>
	<view>
		 
		<view class="container">
			<view class="suspendCardPanel">
				<view class="h30"></view>
				<view class="line50 f26 center suspendCardNum" >
					可用停卡次数：{{stoppableCardQuantity}}次
				</view>
				<image :src= "imageRootUrl + 'image/member/member.png'"  class="memberimg"></image>
				<view class="h20"></view>
				<view class="line40 center shopMemberCar">
					三明市儿童书屋会员卡
				</view>
			</view>
			<view class="h20"></view>
			<picker @change="bindPickerChange" :value="index" :range="suspendCardTypeList" range-key="name" ref="suspendCardType">
				<view class="btnsuspendCard">
					办理停卡
				</view>
			</picker>
			<view class="h20"></view>
			<view class="contentPanel ">
				<view class="h10"></view>
				<view class="line60">
					<view style="float: left;">
						<image :src= "imageRootUrl + 'image/member/suspendCardRecord.png'"  class="size50" style="margin-top: 5rpx;"></image>
					</view>
					<view class="f30" style="float: left;font-weight: bold; margin-left: 5rpx;">
						停卡变更记录
					</view>
				</view>
				<view class="h10"></view>
				<view class="line110 recordPanel" v-for="(item,index) in list" :key="index">
					<view class="line100 center recordgreenleft" >
						<view class="line50 f28 number" >次数</view>
						<block v-if="item.type == 'jia'">
							<view class="line40 f36 numbergreen">+{{item.number}}</view>
						</block>
						<block v-if="item.type == 'jian'">
							<view class="line40 f36 numberRed">-{{item.number}}</view>
						</block>
					</view>
					<view class="recordright">
						<view class="h10"></view>
						<view class="line40 f28 recordRightTitle">{{item.title}}</view>
						<view class="line30 f24 recordRightTime">{{item.time}}</view>
						<view class="h20"></view>
					</view>
					<view class="h10"></view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import common from "@/js/common.js";
	import config from "@/js/config.js";
	import util from "@/js/util.js";
	let statusBarHeight = uni.getSystemInfoSync().statusBarHeight; // 状态栏 高度
	let that = null;
	export default {
		data() {
			return {
				url: "http://admin.8630.net/WYB/UploadFile/headerTopbg.png",
				imageRootUrl:config.imageRootUrl,//图片地址
				statusBarHeight, // 状态栏 高度
				head_is_fixed: 0,
				suspendCardTypeList:[
					{code:'1',name:'停卡一次（30天)'},{code:'2',name:'停卡二次（60天)'},{code:'3',name:'停卡三次（90天)'},
				], 
				index:0,	
				list:[
					{id:1,number:"3",title:"新会员赠送",time:'2012年12月12日',type:'jia',},
					{id:2,number:"5",title:"新会员赠送",time:'2012年12月12日',type:'jia',},
					{id:3,number:"4",title:"使用",time:'2012年12月12日',type:'jian',},
					{id:4,number:"9",title:"使用",time:'2012年12月12日',type:'jian',},
					{id:5,number:"12",title:"使用",time:'2012年12月12日',type:'jian',},
					{id:6,number:"15",title:"新会员赠送",time:'2012年12月12日',type:'jia',},
				],
				stoppableCardQuantity:0,//停卡次数
			};
		},
		onReady() {

		},
		onShow() {

		},
		onPageScroll(e) {
			that.head_is_fixed = e.scrollTop > 0 ? 1 : 0
		},
		onLoad() {
			that = this;
			that.GetMemberCard();//绑定停卡次数
			that.bindData(); //
		},
		methods: {
			GetMemberCard(){
				var data = {};
				data.id = 7
				common.call("GetViewMember", data, function(result) {
					console.log(result.data.stoppableCardQuantity)
					if(result.data.stoppableCardQuantity!=null){
						that.stoppableCardQuantity = result.data.stoppableCardQuantity
					}else{
						that.stoppableCardQuantity = 0
					}
				});
			},
			bindData() {
				var data = {};
				data.memberId = 7
				common.call("GetViewMemberCardDisableLogList", data, function(result) {
					console.log(result)
					
				});
			},
			bindPickerChange: function(e) {//性别选择器
			    console.log('picker发送选择改变，携带值为', e.detail.value)
				
			},
			clickToBack() {
				uni.navigateBack();
			},
		},
	};
</script>

<style lang="scss">
	@import url("/css/common.css");

	page {
		background: #5EAC59;
	}

	.container {
		
	}
	.suspendCardPanel{
		background: -webkit-linear-gradient(left,#FFD25E,#FFF4CD,#FFD540);
		width: 672rpx; height: 392rpx;  border-radius: 40rpx; text-align: center;
	}
	.suspendCardNum{
		background-color: #EEBA00; color: #FFFFFF;border-radius: 60rpx;width: 270rpx; margin-left: 40rpx;
	}
	.memberimg{
		width: 200rpx; height: 190rpx;  border-radius: 40rpx;
	}
	.shopMemberCar{
		color: #AA5200; font-weight: bold;
	}
	.btnsuspendCard{
		background-color: #FE613D;
		text-align: center;
		height: 80rpx;
		font-size: 32rpx;
		font-weight: 600;
		color: #FFFFFF;
		line-height: 80rpx;
		border-radius: 50rpx;
	}
	.recordPanel{
		border-bottom: 1rpx solid #F3F3F3; margin-top: 20rpx;
	}
	.recordgreenleft{
		float: left; width: 100rpx; background: rgba(83,215,118,0.1); border-radius: 16rpx;
	}
	.recordRedLeft{
		float: left; width: 100rpx; background: rgba(254,97,61,0.1); border-radius: 16rpx;
	}
	.number{
		font-weight: 600;
	}
	.numbergreen{
		color: #53D776;font-weight: bold;
	}
	.numberRed{
		color: #FE613D;font-weight: bold;
	}
	.recordright{
		float: left; margin-left: 20rpx;
	}
	.recordRightTitle{
		font-weight: 600;
	}
	.recordRightTime{
		color: #999999;font-weight: bold;
	}
</style>